<template>
    <div>
    <div class="page-container">
      <nav-header></nav-header>
      <div class="container">
        <div class="page-title-normal">
          <h2 class="page-title-h2"><span></span></h2>
        </div>
        <!--  -->
        <div class="check-step">
          <ul>
            <li class="cur"><span></span> </li>
            <li class="cur"><span></span> </li>
            <li class="cur"><span></span> </li>
            <li class="cur"><span></span> </li>
          </ul>
        </div>

        <div class="order-create">
          <div class="order-create-pic"><img src="/static/ok-2.png" alt=""></div>
          <div class="order-create-main">
            <h3>! <br>!</h3>
            <p>
              <span>ID{{orderId}}</span>
              <span>{{orderTotal|currency('')}}</span>
            </p>
            <div class="order-create-btn-wrap">
              <div class="btn-l-wrap">
                <router-link class="btn btn--m" to="/cart"></router-link>
              </div>
              <div class="btn-r-wrap">
                <router-link class="btn btn--m" to="/"></router-link>
              </div>
            </div>
          </div>
        </div>
      </div>
      </div>
      <nav-footer class="noPadding"></nav-footer>
    </div>
</template>
<script>

    const NavHeader = resolve => require(['@/components/Header'], resolve)
    const NavFooter = resolve => require(['@/components/Footer'], resolve)
    const NavBread = resolve => require(['@/components/Bread'], resolve)
    
    import axios from 'axios'
    export default{
        data(){
            return{
                orderId:'',
                orderTotal:0
            }
        },
        components:{
          NavHeader,
          NavFooter,
          NavBread
        },
        mounted(){
            const orderId = this.$route.query.orderId
            if(!orderId){
              return;
            }
            axios.get("/users/orderDetail",{
                params:{
                  orderId:orderId
                }
            })
            .then(res => res.data)
            .then(data => {
                if(data.status === '200'){
                    this.orderId = orderId
                    this.orderTotal = data.result.orderTotal
                }
            })
        }
    }
</script>


